* {
  box-sizing: border-box;
}

html {
  font-family: "Roboto", Arial, sans-serif;
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

body,
p {
  margin: 0;
}

a {
  display: block;
  color: inherit;
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
